  width: 100vw;
  height: 100vh;
  opacity: 1;
  visibility: visible;
  pointer-events: auto;
  background-color: rgba(0, 0, 0, .6);
  justify-content: center;
  align-items: center;
  transition: opacity .25s, visibility .25s;
  display: flex;
  position: fixed;
